//
// Variables
//

$alert-primary-enable: true !default;
$alert-success-enable: true !default;
$alert-danger-enable: true !default;

//
// Component
//

@mixin alert() {
  .alert {
    display: flex;
    padding: $global-whitespace--regular;
    border-radius: $global-border-radius;
    border: 1px solid $global-primary;
    background: $global-background;

    > :last-child {
      margin-bottom: 0;
    }

    @if $alert-primary-enable {
      &--primary {
        background: $global-primary;
        border-color: $global-primary;
        color: $global-contrast;
        @include hook-alert-primary();

        .alert-close {
          color: $global-contrast;

          &:hover,
          &:focus {
            color: $global-contrast;
          }
        }
      }
    }

    @if $alert-success-enable {
      &--success {
        background: $global-success;
        border-color: $global-success;
        color: $global-contrast;
        @include hook-alert-success();

        .alert-close {
          color: $global-contrast;

          &:hover,
          &:focus {
            color: $global-contrast;
          }
        }
      }
    }

    @if $alert-danger-enable {
      &--danger {
        background: $global-danger;
        border-color: $global-danger;
        color: $global-contrast;
        @include hook-alert-danger();

        .alert-close {
          color: $global-contrast;

          &:hover,
          &:focus {
            color: $global-contrast;
          }
        }
      }
    }

    &-content {
      flex: 0 1 100%;

      > svg {
        margin-right: $global-whitespace--small;
      }
    }

    &-right {
      flex: 0 0 auto;
      padding-left: $global-whitespace--small;
    }

    &-close {
      display: inline-block;
      color: $global-primary;

      &:hover,
      &:focus {
        color: $global-primary;
      }
    }

    @include hook-alert();
  }

  // Add margin if adjacent element
  * + .alert {
    margin-top: $global-whitespace--regular;
  }
}
