//
// Component
//

@mixin contrast() {
  // Placeholders
  %contrast-link-styles {
    color: $global-contrast-link !important;
    position: relative;

    &:hover,
    &:focus,
    &.is-active {
      color: $global-contrast-link !important;
    }
  }

  .u-contrast {
    // Text
    color: $global-contrast;

    // Links
    a {
      &:not([class]) {
        @extend %contrast-link-styles;
      }
    }

    .u-link {
      @extend %contrast-link-styles;
    }

    b,
    strong,
    .u-textBold {
      color: $global-contrast;
    }

    i,
    em {
      color: $global-contrast-muted;
    }

    // Code
    :not(pre) > code,
    :not(pre) > kbd,
    :not(pre) > samp {
      color: $global-contrast;
      border-color: $global-contrast-border;
      background: transparent;
    }

    // Headings
    h1,
    h2,
    h3,
    h4 {
      color: $global-contrast;
    }

    hr {
      border-top-color: $global-contrast-border;
    }

    @include hook-contrast();
  }
}
