//
// Component
//

@mixin miscUtility() {
  // Overflow container
  .u-overflowContainer {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
  }

  .u-overflowHidden {
    overflow: hidden;
  }

  // Inline Block
  .u-inlineBlock {
    display: inline-block !important;
  }

  // Block
  .u-block {
    display: block !important;
  }

  @include hook-utility-misc();
}
