//
// Variables
//

$offcanvas-background: rgba($global-dark-overlay, 0.5) !default;
$offcanvas-panel-background: $global-background !default;
$offcanvas-panel-border: 1px solid $global-border !default;

$offcanvas-panel-width: 300px !default;

//
// Component
//

@mixin offCanvas() {
  // Body styles to prevent scrolling
  .has-openOffCanvas {
    height: 100%;
    overflow: hidden;
  }

  // 1. JS styles
  .offCanvas {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: $offcanvas-background;
    z-index: 10;

    // 1.
    opacity: 0;
    display: none;

    &--right {
      .offCanvas-panel {
        left: auto;
        right: 0;
        transform: translateX(100%);
        border-right: 0;
        border-left: $offcanvas-panel-border;
      }
    }

    &-panel {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background: $offcanvas-panel-background;
      border-right: $offcanvas-panel-border;
      padding: $global-whitespace--large;
      z-index: 5;
      overflow-y: auto;
      -webkit-overflow-scrolling: touch;
      // GSAP
      transform: translateX(-100%);
      opacity: 0;

      @media (min-width: $global-viewport--medium) {
        width: $offcanvas-panel-width;
      }

      * {
        &:last-child {
          margin-bottom: 0;
        }

        > * {
          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      @include hook-offcanvas-panel();
    }

    &-close {
      position: absolute;
      top: $global-whitespace--large;
      right: $global-whitespace--large;
      color: $global-muted;
      z-index: 10;

      &:hover,
      &:focus {
        color: $global-font-color;
      }
    }

    @include hook-offcanvas();
  }
}
