//
// Variables
//

$icon-size: 1em !default;
$icon-size-small: 0.65em !default;
$icon-size-large: 1.5em !default;
$icon-size-xlarge: 2em !default;
$icon-size-standalone: 50px !default;

//
// Component
//

@mixin icon() {
  // 1. Fix Firefox blurry SVG rendering: https://bugzilla.mozilla.org/show_bug.cgi?id=1046835
  .icon {
    fill: currentColor;
    size: $icon-size;
    line-height: 1;
    position: relative;
    top: -0.1em;
    max-width: 100%;
    // 1
    transform: translate(0, 0);

    a & {
      pointer-events: none;
    }

    &--small {
      size: $icon-size-small;
    }

    &--large {
      size: $icon-size-large;
    }

    &--xlarge {
      size: $icon-size-xlarge;
    }

    // Used for img; not sprites
    &--standalone {
      width: 100%;
      max-width: $icon-size-standalone;
      height: $icon-size-standalone;
    }

    @include hook-icon();
  }
}
